.authContainer {
    width: 250vh;
    height: 100vh;
    background-color: rgb(76, 148, 241);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: "Open Sans";
    overflow: hidden;
}
.authTitle {
    font-size: 30px;
    color: #fff;
    letter-spacing: .2em;
    margin: 0;
}
.authSubTitle {
    font-size: 15px;
    margin: 0;
    margin-bottom: 3em;
}
.loginContainer {
    display: flex;
    align-items: center;
    margin-bottom: 1em;
}
.loginBlank {
    display: flex;
    flex-direction: column;
}
.loginBlank input {
    font-size: 15px;
    border: none;
    border-radius: .3em;
    margin: .3em;
    padding: .3em;
}
.loginBlank input:focus {
    outline: none;
}
.loginButton {
    font-size: 15px;
    border: none;
    border-radius: .3em;
    color: #fff;
    background-color: #000;
    margin: .3em;
    padding: .5em .3em;
}
.loginButton:hover {
    background-color: rgb(255, 254, 254);
    color: #000;
    transition: all .3s;
}
.loginSwitching {
    font-size: 15px;
    margin-bottom: 1.5em;
    cursor: pointer;
}
.loginSwitching:hover {
    color: #fff;
    transition: all .3s;
}
.otherLoginContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.otherButton {
    font-size: 14px;
    display: flex;
    align-items: center;
    border: none;
    border-radius: .3em;
    margin: .3em;
    padding: .3em;
    width: 100%;
    background-color: #fff;
    cursor: pointer;
}
.otherButton:hover {
    background-color: rgb(203, 227, 248);
    transition: all .3s;
}
.otherButton i {
    font-size: 15px;
    color: rgb(76, 148, 241);
    margin: 0;
    padding: 0;
    margin-left: 1em;
}
.nweetContainer {
    display: flex;
    flex-direction: column;
    font-family: "Open Sans";
    font-size: 15px;
    border-bottom: 1px solid rgb(224, 222, 222);
    padding: .5em 1em;
}
.nweetUser {
    font-size: 15px;
}
.nweetText {
    font-size: 15px;
    font-weight: 300;
}
.nweetImage {
    width: 100%;
    height: 100%;
    margin-top: 1em;
    margin-bottom: 1em;
}
.nweetButton {
    align-self: flex-end;
}
.nweetButton button {
    border: none;
    background-color: rgba(0, 0, 0, 0);
}
.nweetButton i {
    font-size: 15px;
    padding: .5em;
    margin: 0;
}.nweetButton i:hover {
    color: #fff;
    background-color: rgb(76, 148, 241);
}


.nweetEditContainer {
    display: flex;
    flex-direction: column;
}
.nweetEditText {
    border-radius: .5em;
    border: 1px solid rgb(76, 148, 241);
    font-family: "Open Sans";
    font-size: 15px;
    padding: .5em;
    resize: none;
    margin-bottom: 1em;
}
.nweetEditText:focus {
    outline: none;
}
.nweetEditBtnContainer {
    align-self: flex-end;
}
.nweetEditCancel {
    font-family: "Open Sans";
    font-size: 13px;
    border: none;
    background-color: #000;
    color: #fff;
    padding: .3em .5em;
    border-radius: .7em;
    margin-right: .5em;
}
.nweetEditCancel:hover {
    background-color: rgb(39, 39, 39);
    transition: all .3s;
}
.nweetEditUpdate {
    border: none;
    background-color: rgba(0, 0, 0, 0);
}
.nweetEditUpdate i {
    font-size: 15px;
    margin: 0;
    padding: .5em;
}
.nweetEditUpdate i:hover {
    color: #fff;
    background-color: rgb(76, 148, 241);
}
.container {
    display: flex;
    flex-direction: column;
    width: 40%;
    height: 100vh;
    border-left: 1px solid rgb(224, 222, 222);
    border-right: 1px solid rgb(224, 222, 222);
    position: relative;
}
.form {
    background-color: rgb(76, 148, 241);
    color: #fff;
    padding: 2em 1em 1em 1em;
    display: flex;
    flex-direction: column;
    transition: margin-Top .5s;
}
.nweets {
    overflow-y: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;
}
.nweets::-webkit-scrollbar {
    display: none;
}

.inputText {
    border-radius: .5em;
    border: none;
    font-family: "Open Sans";
    font-size: 15px;
    padding: .5em;
    resize: none;
    margin-bottom: 1em;
}
.inputText:focus {
    outline: none;
}
input[type="file"] {
    display: none;
}
.custom-file-upload {
    color: rgb(0, 0, 0);
    align-self: flex-start;
    background-color: rgb(255, 255, 255);
    border-radius: 1em;
    border: none;
    padding: .3em 0;
    cursor: pointer;
    margin-bottom: .5em;
}
.custom-file-upload i {
    font-size: 15px;
}
.custom-file-upload:hover {
    background-color: rgb(236, 233, 233);
    transition: all .3s;
}
.inputSubmit {
    align-self: flex-end;
    border: none;
    background-color: rgba(0, 0, 0, 0);
}
.inputSubmit i {
    color: rgb(255, 255, 255);
    font-size: 25px;
    cursor: pointer;
    margin: 0;
    padding: .3em;
}
.inputSubmit i:hover {
    color: rgb(76, 148, 241);
    background-color: rgb(255, 255, 255);
    transition: all .3s;
}
.attachmentContainer {
    width: 125px;
    height: 50px;
}
.attachmentInsideContainer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
.attachmentImage {
    width: 50px;
    height: 50px;
}
.attachmentButton {
    font-size: 14px;
    font-family: "Open Sans";
    color: #000;
    background-color: #fff;
    border-radius: 1em;
    border: none;
    padding: .3em .5em;
}
.attachmentButton:hover {
    background-color: rgb(236, 233, 233);
    transition: all .3s;
}
.hiddenContainer {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
    background-color: rgb(76, 148, 241);
}
.hiddenMenu {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 20px;
    width: 20px;
    margin: 1em;
    cursor: pointer;
    transition: all .3s;
}
.hiddenMenu1, .hiddenMenu3 {
    width: 100%;
    height: 4px;
    background-color: #fff;
    border-radius: 3em;
}

@media only screen and (max-width: 769px) {
    .container {
        width: 60%;
    }
} 
@media only screen and (max-width: 426px) {
    .container {
        width: 90%;
    }
}
.profileContainer {
    display: flex;
    flex-direction: column;
    width: 40%;
    height: 100vh;
    border-left: 1px solid rgb(224, 222, 222);
    border-right: 1px solid rgb(224, 222, 222);
}
.profileSignContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid rgb(224, 222, 222);
    background-color: rgb(76, 148, 241);
    color: #fff;
    font-family: "Open Sans";
    transition: margin-top .5s;
}
.profileSignButton {
    margin-top: 3em;
    font-size: 15px;
    border: none;
    background-color: #000;
    color: #fff;
    padding: .5em .8em;
    border-radius: 1em;
}
.profileSignButton:hover {
    background-color: rgb(41, 41, 41);
    transition: all .3s;
}
.sensor {
    width: 100%;
    height: .1px;
}
.nweets {
    overflow-y: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;
}
.nweets::-webkit-scrollbar {
    display: none;
}

@media only screen and (max-width: 769px) {
    .profileContainer {
        width: 60%;
    }
}
@media only screen and (max-width: 426px) {
    .profileContainer {
        width: 90%;
    }
}
nav {
    padding-top: 3em;
    height: 100%;
    width: 30%;
}
ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0;
    margin: 0;
}
li {
    margin: .5em 2em;
}
i {
    font-size: 35px;
    color: rgb(29, 118, 235);
    margin: 0 1em;
    padding: .3em;
    border-radius: 1em;
}
.Profile {
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-size: 24px;
    font-family: "Open Sans";
    margin: 1em;
    padding: .3em;
    border-radius: 1em;
}
i:hover, .Profile:hover {
    background-color: rgb(224, 222, 222);
    transition: all .3s;
}

@media only screen and (max-width: 1025px) {
    .Profile {
        font-size: 18px;
    }
}

@media only screen and (max-width: 769px) {
    nav {
        width: 40%;
    }
}
@media only screen and (max-width: 426px) {
    nav {
        width: 10%;
    }
    li {
        margin: 0;
    }
    i {
        font-size: 25px;
        margin: 0;
        padding: .3em;
    }
    .Profile {
        margin: 0;
        padding: 0;
    }
    .Profile i {
        margin: 0;
    }
}
HashRouter {
    display: flex;
}
.AppOutsideContainer {
    display: flex;
}
.AppContainer {
    display: flex;
    flex-direction: column;
    width: 30%;
    height: 100vh;
    position: relative;
}
.AppContainerTitle {
    font-family: "Open Sans";
    background-color: rgb(211, 53, 53);
    text-align: center;
    color: #fff;
    padding: .95em;
    margin: 0;
}
footer {
    width: 100%;
    position: absolute;
    bottom: 0;
    background-color: #fff;
    border-top: 1px solid rgb(224, 222, 222);
    z-index: 2;
}
.newsContainer {
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;
}
.newsContainer::-webkit-scrollbar {
    display: none;
}
.newsArticle {
    width: 90%;
    padding: 1em;
    margin-bottom: 1em;
    border: 1px solid rgb(224, 222, 222);
}
.newsArticle a {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
}
.newsImage {
    width: 100%;
    height: 200px;
}
.newsTitle {
    color: #000;
    font-family: "Open Sans";

}
@media only screen and (max-width: 769px) {
    .AppContainer {
        display: none;
    }
}
